<template>
        <v-container>
                   <v-layout>
                       <v-flex
                               class="mr-auto"
                       xs5 sm5 md3 lg3
                       >
                           <div >
                               <span>ຈັດລຽງຕາມປະເພດ:</span>
                               <v-select
                                       @change="filterCategory(1)"
                                       v-model="cateId"
                                       :items="categories"
                                       item-text="name"
                                       item-value="_id"
                                       solo
                               ></v-select>
                           </div>
                       </v-flex>
                       <v-flex
                               class="ml-auto"
                               xs5 sm5 md3 lg3
                       >

                       </v-flex>
                   </v-layout>

            <v-row wrap>
                <v-col xs="12" sm="6" md="3" lg="3"
                        v-for="(port, index) in portfolios" :key="index">
                        <v-card
                                class="portfolio-card mx-auto animated bounceInUp"
                                max-width="374"
                        >
                            <router-link :to="{ name: 'Details' , params: { id: port._id } }">
                                <v-img
                                        :src="$server + port.images[0]"
                                        :lazy-src="$server + port.images[0]"
                                        :height="$vuetify.breakpoint.mdAndUp ? 190 : 300"
                                        class="portfolio-img"
                                >
                                    <template v-slot:placeholder>
                                        <v-row
                                                class="fill-height"
                                                align="center"
                                                justify="center"
                                        >
                                            <v-progress-circular size="60" indeterminate
                                                                 color="primary"
                                            ></v-progress-circular>
                                        </v-row>
                                    </template>
                                </v-img>
                            </router-link>
                            <small class="ma-3">{{ port.title }}</small>
                            <v-card-actions>
                                <v-rating
                                        :value="
                                     ((port.reviews.map(i=>i.rating)).reduce((a, b) => a + b, 0)) / port.reviews.length"
                                        readonly
                                        dense
                                        half-increments
                                        color="yellow darken-3"
                                        background-color="grey darken-1"
                                        hover
                                        size="15"
                                ></v-rating>
                                <span class="warning--text ml-1 mt-1">{{ ((port.reviews.map(i=>i.rating)).reduce((a, b) => a + b, 0)) / port.reviews.length ?
                                 ((port.reviews.map(i=>i.rating)).reduce((a, b) => a + b, 0)) / port.reviews.length :
                                 '' }}
                             </span>
                                <span class="grey--text mx-2 mt-1">
                              ( {{ port.reviews.length }} )
                                    </span>
                            </v-card-actions>
                            <!--                        <v-card-text v-html="port.description.substring(0,40) + '...'"></v-card-text>-->
                            <v-divider/>
                            <v-card-actions>
                                <v-avatar>
                                    <v-img  width="40"
                                            height="40"
                                            :src="port.author.image ? $server + port.author.image:'https://www.w3schools.com/howto/img_avatar.png'"
                                    ></v-img>
                                </v-avatar>
                                <v-btn @click="filterCate(port.categoryId._id)"
                                       class="primary--text font-weight-bold ml-auto v-btn--outlined"
                                       text>{{
                                    port.categoryId ?
                                    port.categoryId.name: ''}}</v-btn>
                            </v-card-actions>
                        </v-card>
                </v-col>
                <v-col v-if="portfolios.length <= 0">
                   <p class="primary--text"> No portfolio found</p>
                </v-col>
            </v-row>
            <div v-if="portfolios.length >= 1">
                <template v-if="!catePaginate" >
                    <div class="text-center" @click="fetchPortfolio" >
                        <v-pagination
                                v-model="pagination.page"
                                :length="pagination.length"
                        ></v-pagination>
                    </div>
                </template>
                <template v-if="catePaginate">
                    <div class="text-center" @click="filterCategory(pagination.page)">
                        <v-pagination
                                v-model="pagination.page"
                                :length="pagination.length"
                        ></v-pagination>
                    </div>
                </template>
            </div>
        </v-container>
</template>

<script>
    import AOS from 'aos'
    import 'aos/dist/aos.css'
    export default {
        props: [
            'portfolios',
            'categories',
            'categoryId',
            'pagination',
            'catePaginate',
            'fetchPortfolio',
            'filterCategory',
        ],
        data: () => ({
            show: true
        }),
        computed: {
            cateId: {
                get () {
                    return this.categoryId
                },
                 set (value) {
                    this.$store.commit('SetCATEGORY', value)
                }
            }
        },
        methods: {
            filterCate (value) {
                this.$store.commit('SetCATEGORY', value)
                this.filterCategory(1)
            }
        },
        created () {
            AOS.init()
        }
    }
</script>

